.container {
    max-width: 1200px;
    margin: 0 auto;
}

.hero {
    background-image: url("https://images.pexels.com/photos/450035/pexels-photo-450035.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom center;
    height: 100vh;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    margin-bottom: 20px;
    z-index: -2;
    
    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.6);
        z-index: -1;
    }

    h1{
      font-size: 46px;
      margin: -20px 0 20px;
    }

    p{
      font-size: 20px;
      letter-spacing: 1px;
    }
}

.content h2,.content h3{
  font-size: 150%;
  margin: 10px 0;
}

.content p{
  color:#555;
  line-height: 30px;
  letter-spacing: 1.2px;
}
